*{
    margin-bottom: 10px;
}

.profile-container {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: fit-content;
  }
  
  .profile-parent {
    display: flex;
    align-items: center;
    color: rgb(0, 0, 0);
  }
  
  .profile-details-name {
    font-size: 24px;
    font-family: "poppins SemiBold";
  }
  
  .profile-details-role {
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
  }
  
  .profile-details-role h1 {
    font-size: 40px;
    font-family: cursive;
  }
  
  .primary-text {
    color: (--white);
  }
  
  .highlighted-text {
    color: #ff5823;
  }
  
  .profile-role-tagline {
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
  }
  
  .profile-options .highlighted-btn {
      margin: 0 0 0 28px;
    }
  
  .profile-options .highlighted-btn:hover {
    background-color: #333;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    margin: 0 0 0 28px;
    border-radius: 5px;
    font-size: 16px;
    background-color: rgb(173, 173, 173);
    color: rgb(0, 0, 0);
  }
  

   .btn {
    display: inline-block;
    padding: 10px 20px;
    /* border: none; */
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .primary-btn {
    background-color: #ff5823;
    color: #fff;
  }
  
  .highlighted-btn {
    background-color: #fff;
    color: #333;
    border: 1px solid #333;
  }
  
  .primary-btn:hover,
  .primary-btn:focus,
  .highlighted-btn:hover,
  .highlighted-btn:focus {
    outline: none;
  }
  
  .profile-options {
    margin-top: 20px;
  }

  .profile-picture{
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 1px 0px var(--orange);
      border-radius: 50%;
      height: 380px;
      width: 380px;
      margin: 0 0px 0 100px;
  }
  
  .profile-picture-background{
      height: 92%;
      width: 92%;
      background-image: url(../../assets/profilephoto.jpg);
      background-size: cover;
      border-radius: 50%;
      background-repeat: no-repeat;
      background-position: center;
  }
  
  .profile-picture-background:hover{
      transform: scale(1.07);
      transition: 1s ease-out;
  }
  
  .colz .colz-icon i{
      color: rgb(0, 0, 0);
      padding: 8px;
      padding-left: 0px;
  }
  
  .colz .colz-icon i:hover{
      transform: scale(1.5);
  }
  
  @media screen and (max-width: 1110px){
      .profile-picture{
          margin: 0 0px 0 80px;
      }
      .profile-details-name{
          font-size: 20px;
      }
      .profile-details-role .primary-text{
          font-size: 12px;
      }
      .profile-option .btn{
          width: 120px;
          padding: 11px 0;
          z-index: 700;
      }
  }
  
  
  @media screen and (max-width: 910px){
      .profile-picture{
          height: 320px;
          width: 320px;
          margin: 0 0 0 4px;
      }
  }
  
  @media screen and (max-width: 810px){
      .profile-picture{
          margin: 0 !important;
      }
      .profile-parent{
          flex-direction: column-reverse;
          margin: 40px 0 0 0;
      }
      .profile-options{
          display: flex;
          justify-content: space-between;
      }
      .profile-details{
          margin: 25px 0 0;
      }
      .profile-options .highlighted-btn{
          margin: 0 !important;
      }
      .profile-options,
      .profile-details-role{
          text-align: center;
      }
  }
  
  @media screen and (max-width: 375px){
      .profile-picture{
          height: 275px;
          width: 275px;
          margin-top: -90px;
      }
      .profile-details{
          width: 90%;
      }
  }